export class WeaponStat {
    id: number;
    name: string;
    attackType: string;
    altMode: boolean;
    type: string;
    stopOnHit: boolean;
    canCombo: boolean;
    canFlinch: boolean;
    pointCost: number;
    damageNoArmor: WeaponDamage;
    damageLightArmor: WeaponDamage;
    damageMediumArmor: WeaponDamage;
    damageHeavyArmor: WeaponDamage;
    computedDamage: WeaponComputedDamage;

    staminaCost: WeaponStaminaCost;
    speed: WeaponSpeed;
    computedSpeed: WeaponComputedSpeed;
    length: number;
}

export class WeaponComputedDamage {
    averageDamage: number;
    dps: number;
    cDps: number;
    averageDps: number;
}

export class WeaponDamage {
    head: number;
    torso: number;
    legs: number;
}

export class DamageCollection {
    armorType: string;
    values: WeaponDamage[];
}

export class WeaponStaminaCost {
    miss: number; 
    feint: number;
    morph: number;
    drain: number;
    parryDrainNegation: number;
}

export class WeaponSpeed {
    windup: number;
    combo: number;
    release: number;
    recovery: number;
}

export class WeaponComputedSpeed {
    attack: number;
    combo: number;
}

export class DisplayWeapon extends WeaponStat {
    isSelected: boolean = false;
    data: WeaponStat;

    constructor(data: WeaponStat) {
        super();
        Object.assign(this, data);
    }
    
}
