



































































import Vue from 'vue';
import { Component, Watch } from 'vue-property-decorator';
import Weapon from './partial/weapon.vue';
import AttributeBars from './partial/attribute-bars.vue';
import {WeaponStat, DisplayWeapon} from '../ts/types';

@Component({
    components: {Weapon, AttributeBars}
})
export default class App extends Vue {
    isLoading = true;
    data: {marjorDimension: string, values: any[]} = null;
    dataStr = null;

    weapons: WeaponStat[] = [];
    displayWeapons: DisplayWeapon[] = [];

    filteredWeapons: DisplayWeapon[] = [];
    search: string = '';

    get selectedWeapons() {
        return this.displayWeapons.filter(o => o.isSelected);
    }

    highestComputedAttackSpeed = 0;
    highestWeaponLength = 0;

    @Watch('search')
    onSearchChanged() {
        const search = this.search.toLowerCase();
        const result: DisplayWeapon[] = [];

        for (let i = 0; i < this.weapons.length; i++) {
            if (search.length == 0 || this.weapons[i].name.toLowerCase().indexOf(this.search) != -1) {
                result.push(this.displayWeapons[i]);
            }
        }

        this.filteredWeapons = result;
    }

    async loadSpreadsheetData() {
        const spreadsheetId = '1HXS_aF9CKeGKg6OecwCw3WobZWcTjDkp_4ipN1vnyVk';
        // The API key shown in the URL is limited to https://janne252.github.io
        const sheetName = 'Melee Weapons [Advanced]';
        const apiKey = 'AIzaSyCzebhzmLja7pNdyqTjbd0Q9llFzv2tPjE';
        
        const url = `https://sheets.googleapis.com/v4/spreadsheets/${spreadsheetId}/values/${sheetName}?key=${apiKey}`;
         
        const request = await fetch(url);
        this.dataStr = await request.text();
        this.data = JSON.parse(this.dataStr);
        console.log(this.data);

        const headerRowCount = 2;

        let rowIndex = -1;
        for (let row of this.data.values as string[]) {
            rowIndex++;

            if (rowIndex < headerRowCount) {
                continue;
            }
            
            let colIndex = 0;
            const stat: WeaponStat = {
                id: rowIndex,
                name:       row[0],
                attackType: row[1], 
                altMode:    row[2].toLowerCase() == 'on',   
                type:       row[3],                           
                stopOnHit:  row[4].toLowerCase() == 'yes',          
                canCombo:   row[5].toLowerCase() == 'yes',          
                canFlinch:  row[6].toLowerCase() == 'yes',          
                pointCost:  parseInt(row[7]),    
                // 8: value
                damageNoArmor: {
                    head: parseInt(row[9]),
                    torso: parseInt(row[10]),
                    legs: parseInt(row[11]),
                },
                damageLightArmor: {
                    head: parseInt(row[12]),
                    torso: parseInt(row[13]),
                    legs: parseInt(row[14]),
                },
                damageMediumArmor: {
                    head: parseInt(row[15]),
                    torso: parseInt(row[16]),
                    legs: parseInt(row[17]),
                },
                damageHeavyArmor: {
                    head: parseInt(row[18]),
                    torso: parseInt(row[19]),
                    legs: parseInt(row[20]),
                },
                computedDamage: {
                    averageDamage: parseInt(row[21]),
                    dps: parseInt(row[22]),
                    cDps: parseInt(row[23]),
                    averageDps: parseInt(row[24]),
                },
                staminaCost: {
                    miss: parseInt(row[25]),                        
                    feint: parseInt(row[26]),                        
                    morph: parseInt(row[27]),                        
                    drain: parseInt(row[28]),                        
                    parryDrainNegation: parseInt(row[29]),                        
                },
                speed: {
                    windup: parseInt(row[30]),
                    combo: parseInt(row[31]),
                    release: parseInt(row[32]),
                    recovery: parseInt(row[33]),
                },
                computedSpeed: {
                    attack: parseFloat(row[34]),
                    combo: parseFloat(row[36]),
                },
                length: parseInt(row[36])
            }

            this.weapons.push(stat);
        }

        function sortWeapon(a: WeaponStat, b: WeaponStat) {
            const _a = a.name + a.attackType + (a.altMode ? 'B' : 'A');
            const _b = b.name + b.attackType + (b.altMode ? 'B' : 'A');

            if (_a < _b) return -1;
            if (_a > _b) return 1;
            return 0;
        }

        this.weapons.sort(sortWeapon);

        this.highestComputedAttackSpeed = Math.max(...this.weapons.map(o => o.computedSpeed.attack));
        this.highestWeaponLength = Math.max(...this.weapons.map(o => o.length));

        this.displayWeapons = this.weapons.map(o => new DisplayWeapon(o));

        this.onSearchChanged();
        this.isLoading = false;
    }

    mounted() {
        this.loadSpreadsheetData();            
    }

    selectAll() {
        for (let weapon of this.displayWeapons) {
            weapon.isSelected = true;
        }
    }

    unSelectAll() {
        for (let weapon of this.displayWeapons) {
            weapon.isSelected = false;
        }
    }
}
